import BigNumber from 'bignumber.js'
import erc20ABI from 'config/abi/erc20.json'
import masterchefABI from 'config/abi/masterchef.json'
import bnbRewardChefABI from 'config/abi/bnbRewardChef.json'
import multicall from 'utils/multicall'
import farmsConfig from 'config/constants/farms'
import { getBNBRewardChefAddress, getMasterChefAddress } from 'utils/addressHelpers'
import { QuoteToken } from 'config/constants/types'

const CHAIN_ID = process.env.REACT_APP_CHAIN_ID

export const fetchFarmUserAllowances = async (account: string) => {
  const masterChefAdress = getMasterChefAddress()
  const bnbRewardChefAddress = getBNBRewardChefAddress()

  const calls = farmsConfig.map((farm) => {
    const lpContractAddress = farm.isTokenOnly ? farm.tokenAddresses[CHAIN_ID] : farm.lpAddresses[CHAIN_ID]
    return { address: lpContractAddress, name: 'allowance', params: [account, farm.pid === 100 ? bnbRewardChefAddress : masterChefAdress] }
  })

  const rawLpAllowances = await multicall(erc20ABI, calls)
  const parsedLpAllowances = rawLpAllowances.map((lpBalance) => {
    return new BigNumber(lpBalance).toJSON()
  })
  return parsedLpAllowances
}

export const fetchFarmUserTokenBalances = async (account: string) => {
  const calls = farmsConfig.map((farm) => {
    const lpContractAddress = farm.isTokenOnly ? farm.tokenAddresses[CHAIN_ID] : farm.lpAddresses[CHAIN_ID]
    return {
      address: lpContractAddress,
      name: 'balanceOf',
      params: [account],
    }
  })

  const rawTokenBalances = await multicall(erc20ABI, calls)
  const parsedTokenBalances = rawTokenBalances.map((tokenBalance) => {
    return new BigNumber(tokenBalance).toJSON()
  })
  return parsedTokenBalances
}

export const fetchFarmUserStakedBalances = async (account: string) => {
  const masterChefAdress = getMasterChefAddress()
  const bnbRewardChefAddress = getBNBRewardChefAddress()

  const rawStakedBalances = await Promise.all(
    farmsConfig.map(async (farm) => {
      let call
      let abi
      if (farm.pid === 100) {
        abi = bnbRewardChefABI
        call = {
          address: bnbRewardChefAddress,
          name: 'userInfo',
          params: [account],
        }
      } else {
        abi = masterchefABI
        call = {
          address: masterChefAdress,
          name: 'userInfo',
          params: [farm.pid, account],
        }
      }
      const rawStakedBalance = (await multicall(abi, [call]))[0]
      return rawStakedBalance
    })
  )

  // const rawStakedBalances = await multicall(masterchefABI, calls)
  const parsedStakedBalances = rawStakedBalances.map((stakedBalance) => {
    return new BigNumber(stakedBalance[0]._hex).toJSON()
  })
  return parsedStakedBalances
}

export const fetchFarmUserEarnings = async (account: string) => {
  const masterChefAdress = getMasterChefAddress()
  const bnbRewardChefAddress = getBNBRewardChefAddress()

  const rawEarnings = await Promise.all(
    farmsConfig.map(async (farm) => {
      let call
      let abi
      if (farm.tokenSymbol === QuoteToken.BNB) {
        abi = bnbRewardChefABI
        call = {
          address: bnbRewardChefAddress,
          name: 'pendingReward',
          params: [account],
        }
      } else {
        abi = masterchefABI
        call = {
          address: masterChefAdress,
          name: 'pendingMkats',
          params: [farm.pid, account],
        }
      }
      const rawEarning = (await multicall(abi, [call]))[0]

      return rawEarning
    })
  )

  // const rawEarnings = await multicall(masterchefABI, calls)
  const parsedEarnings = rawEarnings.map((earnings) => {
    return new BigNumber(earnings).toJSON()
  })
  return parsedEarnings
}
